// Klasa Validator, ktrej obiektw bdziemy uywa do walidacji formularzy
export class Validator {

  // Konstruktor klasy Validator. W parametrze 'validationConfigs' podajemy tablic obiektw
  // typu ValidationConfig ktre przechowuj informacj o tym jakie pola walidujemy
  // oraz list funkcji walidujcych dla zdefiniowanych pl
  constructor(validationConfigs) {
    this.validationConfigs = validationConfigs;
  }

  // Metoda do ktrej przekazujemy obiekt ktry nastpnie bdzie zwalidowany za pomoc funkcji
  // zdefiniowanych w konfiguracji. Zwraca obiekt typu ValidationResult
  validate(data) {

    let isValid = true;
    let errorMessages = {}; // obiekt zawierajcy list bdw walidacyjnych np: { "title": "title is not valid"}

    // Dla kadej nazwy pola (item) w obiekcie data
    for (let item in data) {

      if (data.hasOwnProperty(item)) { // sprawdza czy to pole faktycznie znajduje si w tym obiekcie

        // Znajdujemy na licie konfiguracji, t ktra jest zdefiniowana dla pola o nazwie okrelonej w zmiennej 'item'
        const config = this.validationConfigs.find(e => e.fieldName === item)
        if (config) {

          // Dla kadej funkcji walidacyjnej w konfiguracji dla tego pola
          for (let validationFunc of config.validationFunctionList) {

            const valFuncErrorMessage = validationFunc(data, item); // Wywoujemu t funkcj... wane aby wszystkie funkcje tego typu miay takie same parametry

            // Jeli zwrcono komunikat walidacyjny to wtedy obiekt jest niepoprawny oraz dodajemy komunikat do obiektu z bdami walidacji
            if (valFuncErrorMessage !== undefined && valFuncErrorMessage !== null && valFuncErrorMessage !== "") {
              isValid = false;
              errorMessages[item] = valFuncErrorMessage;

              break;
            }
          }
        }
      }
    }

    return new ValidationResult(isValid, errorMessages)
  }
}

// Klasa ValidationConfig zawiera konfiguracj walidacji dla pojedynczego pola
// ktrego nazwa znajduje si w polu 'fieldName'. Natomiast w polu 'validationFunctionList'
// znajduje si tablica rodziny funkcji o sygnaturze (obj, fieldName) ktre bd uyte do
// walidacji pola o tej nazwie.
// - fieldName - nazwa pola dla ktrego okrelamy konfiguracj
// - validationFunctionList - lista funkcji za pomoc ktrych pole bdzie walidowane
export class ValidationConfig {
  constructor(fieldName, validationFunctionList) {
    this.fieldName = fieldName;
    this.validationFunctionList = validationFunctionList;
  }
}

// Klasa ValidationResult zawiera informacje o wyniku walidacji obiektu
// - isValid - pole to okrela czy obiekt jest poprawny czy nie
// - errorMessages - obiekt ktry zawiera list komunikatw bdw
class ValidationResult {

  constructor(isValid, errorMessages) {
    this.isValid = isValid;
    this.errorMessages = errorMessages;
  }

  getErrorMessage(fieldName) {
    return this.errorMessages[fieldName];
  }
}
//-------------------FUNKCJE WALIDUJCE------------------------------//
// Funkcja o parametrach (obj, fieldName) suca do sprawdzenia czy pole 'fieldName' w obiekcie 'obj' jest wypenione.
// Jeli nie jest to zwraca komunikat bdu walidacji.
export function validateRequired(obj, fieldName) {
  if (obj[fieldName] === undefined || obj[fieldName] === null || obj[fieldName] === "") {
    return `${fieldName} is required`
  }
}

// Funkcja o parametrach (obj, fieldName) suca do sprawdzenia czy pole 'fieldName' w obiekcie 'obj' zawiera
// maksymaln ilo znakw (10).
// Jeli nie to zwraca komunikat bdu walidacji.
//Funkcja wymaga wicej parametrw, eby je przekaza poprawnie musimy stworzy funkcj opakowujc, ktra zwraca funkcj z odpowiedni liczb parametrw.
function validateMaxLenght(obj, fieldName, maxLenght) {
  if (obj[fieldName].length > maxLenght) {
    return `maximum length: ${maxLenght} characters`
  }
}

export function useValidateMaxLenght(maxLenght) {
  return (obj, fieldName) => validateMaxLenght(obj, fieldName, maxLenght);
}

//Funkcja sprawdzajca, czy godzina 'do' jest wiksza od godziny 'od'

function validateTimeValues(obj, fieldname, taskStartTimeValue) {
  if (obj[fieldname] < taskStartTimeValue) {
    return `invalid time range`
  }
}

export function useValidateTimeValues(taskStartTimeValue) {
  return (obj, fieldname) => validateTimeValues(obj, fieldname, taskStartTimeValue)
}